import { createServerClient, type CookieOptions } from '@supabase/ssr'
import { type NextRequest, NextResponse } from 'next/server'
import { cookies } from 'next/headers'

const protectedPaths = ["/dashboard", "/admin", "/profile"]
const authPaths = ["/auth/login", "/auth/register", "/auth/admin-login"]
const adminPaths = ["/admin"]

export async function middleware(request: NextRequest) {
  const { pathname } = request.nextUrl

  const supabase = createServerClient(
    process.env.NEXT_PUBLIC_SUPABASE_URL!,
    process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!,
    {
      cookies: {
        get(name: string) {
          return request.cookies.get(name)?.value
        },
        set(name: string, value: string, options: CookieOptions) {
          request.cookies.set({
            name,
            value,
            ...options,
          })
        },
        remove(name: string, options: CookieOptions) {
          request.cookies.set({
            name,
            value: '',
            ...options,
          })
        },
      },
    }
  )

  const {
    data: { session },
  } = await supabase.auth.getSession()

  const res = NextResponse.next()

  // Handle authentication for protected routes
  if (protectedPaths.some(path => pathname.startsWith(path))) {
    if (!session) {
      const redirectUrl = new URL("/auth/login", request.url)
      redirectUrl.searchParams.set("redirectTo", pathname)
      return NextResponse.redirect(redirectUrl)
    }

    // Handle admin-only routes
    if (adminPaths.some(path => pathname.startsWith(path))) {
      const userRole = session.user?.user_metadata?.role as string | undefined
      if (userRole !== "admin") {
        return NextResponse.redirect(new URL("/", request.url))
      }
    }
  }

  // Prevent authenticated users from accessing auth pages
  if (authPaths.some(path => pathname.startsWith(path)) && session) {
    const userRole = session.user?.user_metadata?.role as string | undefined
    return NextResponse.redirect(
      new URL(userRole === "admin" ? "/admin" : "/dashboard", request.url)
    )
  }

  // Set cookies
  const cookieStore = cookies()
  cookieStore.getAll().forEach((cookie) => {
    res.headers.append('Set-Cookie', cookie.value)
  })

  return res
}

export const config = {
  matcher: ["/((?!_next/static|_next/image|favicon.ico|.*\\.(?:svg|png|jpg|jpeg|gif|webp)$).*)"],
}
